<template>
  <el-dialog
    width="750px"
    title="新增跟进"
    :model-value="visible"
    class="custom-dialog"
    @close="handleClose"
  >
    <div class="body-container">
      <el-form ref="ruleFormRef" :model="formData" label-position="top">
        <el-form-item label="跟进信息">
          <el-input
            v-model="formData.message"
            maxlength="100"
            class="box-textarea"
            placeholder="请输入"
            show-word-limit
            :rows="4"
            type="textarea"
          />
        </el-form-item>
        <el-form-item label="跟进附件">
          <CustomUpload
            listType="picture-card"
            :limit="5"
            :maxVolume="10"
            v-model:defaultFileList="formData.defaultFileList"
            @emitUpload="handleFile"
            tip="支持.jpg、.jpeg、.png格式，最多只能上传5张"
          >
          </CustomUpload>
        </el-form-item>
      </el-form>
    </div>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="handleConfirm">保 存</el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script lang="ts" setup>
import { shallowReactive, } from "vue";
import { ElMessage } from "element-plus";
import { AddFollowUpApi } from "@api/customer-management-api";
import { LocalStorage } from "jz-tool-lib";

defineProps<{
  visible: boolean;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean): void;
  (e: "emitFunc");
}>();

const { bd_agentId: agentId } = LocalStorage.batchGetItem(["bd_agentId"]);
const formData = shallowReactive({
  message: "",
  defaultFileList: [],
  fileList: [],
});

//关闭
const handleClose = () => {
  emits("update:visible", false);
  formData.message = "";
  formData.defaultFileList = [];
  formData.fileList = []
};

//图片上传
function handleFile(value: any) {
  formData.fileList = value;
}

//保存
const handleConfirm = () => {
  if (formData.message || formData.defaultFileList.length > 0) {
    const params = {
      attachment: formData.fileList.toString(),
      followContent: formData.message,
      followType: 2,
      relevanceId: agentId,
    };
    AddFollowUpApi.getData(params).then((response) => {
      if (response.statusCode === 0) {
        ElMessage.success("新增跟进成功！");
        emits("emitFunc");
        handleClose();
      } else {
        ElMessage.error("新增跟进失败！");
      }
    });
  } else {
    ElMessage.warning("请至少输入跟进信息或上传附件！");
  }
};

</script>
<style lang="scss" scoped>
.box-textarea {
  width: 500px;
}
:deep(.upload .tip) {
  color: #9ca6b9;
}
</style>
