<template>
  <div class="timeline-content">
    <div class="follow-record">
      <h4>跟进记录</h4>
      <el-button type="primary" @click="addFollow">新增跟进</el-button>
    </div>
    <div class="write-time">
      <el-timeline class="timeline" v-if="pageData.list.length">
        <el-timeline-item
          v-for="(item, index) in pageData.list"
          :key="index"
          :timestamp="item.createdTime"
          placement="top"
        >
          <el-card>
            <div class="information">
              <p>跟进信息：{{ item.followContent }}</p>
              <div class="box-item">
                <p class="attachment">跟进附件：</p>
                <div
                  v-if="item.attachment && item.attachment.length > 0"
                  class="attachment-img"
                >
                  <el-image
                    v-for="(url, imgIndex) in item.attachment"
                    :key="imgIndex"
                    class="img-item"
                    :preview-src-list="[url]"
                    :src="url"
                    fit="cover"
                  />
                </div>
                <p v-else>暂无</p>
              </div>
            </div>
          </el-card>
        </el-timeline-item>
      </el-timeline>
      <template v-else>
        <TableEmpty empty-text="暂无数据"></TableEmpty>
      </template>
    </div>
    <!--分页-->
    <PagePagination
      :pageTotal="pageData.pageTotal"
      :pageSize="pageData.pageSize"
      :pageNumber="pageData.pageNumber"
      @emitPageSizeChange="pageSizeChange"
      @emitPageNumberChange="pageNumberChange"
    ></PagePagination>
    <!-- 新增跟进弹框-->
    <addFollowDialog
      v-model:visible="initData.addFollowVisible"
      @emitFunc="getList"
    ></addFollowDialog>
  </div>
</template>
<script lang="ts" setup>
import { reactive } from "vue";
import { LocalStorage, usePage } from "jz-tool-lib";
import { followUpListApi } from "@api/customer-management-api";
import addFollowDialog from "@/components/customer-management/personal-service/add-follow-dialog.vue";

const { bd_agentId: agentId } = LocalStorage.batchGetItem(["bd_agentId"]);
const initData = reactive({
  addFollowVisible: false,
});
const { getList, pageNumberChange, pageSizeChange, pageData } = usePage({
  listApi: followUpListApi,
  props: {
    pageSize: "pageSize",
    pageNumber: "page",
  },
  searchParams: () => {
    return {
      followType: 2,
      relevanceId: agentId,
    };
  },
});
//新增跟进弹框
const addFollow = () => {
  initData.addFollowVisible = true;
};
</script>

<style scoped lang="scss">
.timeline-content {
  flex-grow: 1;
  margin-left: 20px;
  margin-bottom: 0;
  padding: 36px 24px 20px;
  background-color: #ffffff;

  .write-time {
    overflow-y: auto;
    margin-top: 10px;
    height: calc(100% - 100px);
  }
}

.follow-record {
  display: flex;
  justify-content: space-between;
}

.box-item {
  display: flex;

  .img-item {
    width: 100px;
    height: 100px;
    margin-right: 10px;
  }
}

.timeline {
  max-width: 1100px;
  margin-right: 100px;
  padding-left: 5px;
}

.information {
  display: grid;
  grid-template-columns: 350px 1fr;
  height: 100px;
}

.attachment {
  flex: 0 0 70px;
  margin-left: 20px;
}
.attachment-img {
  flex-grow: 1;
  max-height: 105px;
  overflow-y: auto;
}
</style>
